import React from "react";
import { Box, Typography, Paper, Grid } from "@mui/material";
import { styled } from "@mui/material/styles";
import {
  Table,
  TableBody,
  TableContainer,
  TableHead,
  TableRow,
  TableCell,
} from "@mui/material";
import avatar from "../../images/avatar-main.png";
import leave from "../../images/exit.png";
import onsite from "../../images/available.png";
import ontime from "../../images/on-time.jpg";
import absent from "../../images/absent.png";
import working from "../../images/working.png";
import "../css/profile.css";

const ItemPara = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(1),
  textAlign: "left",
  "&:hover": {
    boxShadow: "3px 3px rgba(0,0,0,.1)",
  },
  color: "rgb(12, 0, 57)",
  borderRadius: "20px",
  boxShadow: "none",
}));

function createData(name, data) {
  return { name, data };
}

const rows = [createData("Leave Requests", 23)];

function Dashboard() {
  return (
    <div className="dash-main">
      <Box
        style={{ backgroundColor: "rgb(243, 243, 243)" }}
        component="main"
        sx={{ flexGrow: 1 }}
      >
        <Box sx={{ flexGrow: 1 }}>
          <Grid container spacing={4}>
            <Grid item xs={12} sm={6} md={4}>
              <ItemPara>
                <Typography>
                  <h3 className="profile-h3">Total Employees</h3>
                  <p className="profile-num">30</p>
                  <img src={avatar} className="img-profile" alt="" />
                </Typography>
              </ItemPara>
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
              <ItemPara>
                <Typography>
                  <h3 className="profile-h3">On Site</h3>
                  <p className="profile-num">21</p>
                  <img src={onsite} className="img-profile" alt="" />
                </Typography>
              </ItemPara>
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
              <ItemPara>
                <Typography>
                  <h3 className="profile-h3">Absent</h3>
                  <p className="profile-num">11</p>
                  <img src={absent} className="img-profile" alt="" />
                </Typography>
              </ItemPara>
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
              <ItemPara>
                <Typography>
                  <h3 className="profile-h3">Leave Pending</h3>
                  <p className="profile-num">8</p>
                  <img src={leave} className="img-profile" alt="" />
                </Typography>
              </ItemPara>
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
              <ItemPara>
                <Typography>
                  <h3 className="profile-h3">Late</h3>
                  <p className="profile-num">5</p>
                  <img src={ontime} className="img-profile" alt="" />
                </Typography>
              </ItemPara>
            </Grid>
            <Grid item xs={12} sm={6} md={4}>
              <ItemPara>
                <Typography>
                  <h3 className="profile-h3">Pending Projects</h3>
                  <p className="profile-num">7</p>
                  <img src={working} className="img-profile" alt="" />
                </Typography>
              </ItemPara>
            </Grid>
          </Grid>
          <br />
        </Box>
        {/* Requests Table */}
        <Box sx={{ flexGrow: 1 }}>
          <Grid container spacing={4}>
            <Grid item xs={12} md={12}>
              <ItemPara>
                <TableContainer sx={{ boxShadow: "none" }} component={Paper}>
                  <Table sx={{ minWidth: 300 }} aria-label="simple table">
                    <TableHead>
                      <TableRow>
                        <TableCell
                          sx={{
                            fontWeight: "bold",
                            fontSize: "16px",
                            color: "rgb(19, 71, 129)",
                          }}
                          align="left"
                        >
                          Requests
                        </TableCell>
                        <TableCell
                          sx={{
                            fontWeight: "bold",
                            fontSize: "16px",
                            color: "rgb(19, 71, 129)",
                          }}
                          align="right"
                        >
                          Pending
                        </TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {rows.map((row) => (
                        <TableRow
                          key={row.name}
                          sx={{
                            "&:last-child td, &:last-child th": {
                              border: 0,
                            },
                          }}
                        >
                          <TableCell align="left" component="th" scope="row">
                            {row.name}
                          </TableCell>
                          <TableCell align="right">{row.data}</TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </TableContainer>
              </ItemPara>
            </Grid>
          </Grid>
          <br />
        </Box>
      </Box>
    </div>
  );
}

export default Dashboard;
